// The aspect ratio of the simulated phone when in "desktop mode".
const PHONE_ASPECT_RATIO = 9.0 / 16.0;

// When the aspect ratio of the whole screen is less than this, switch to mobile mode.
const MOBILE_MODE_ASPECT_RATIO = 11.0 / 16.0;

class HeadphonesWarning extends React.Component<
  {},
  { phase: "visible" | "fading" | "gone" }
> {
  constructor(props) {
    super(props);
    this.state = { phase: "visible" };
  }

  componentDidMount() {
    setTimeout(() => {
      this.startFade();
    }, 2000);
  }

  startFade() {
    if (this.state.phase === "visible") {
      this.setState({ phase: "fading" });
      setTimeout(() => {
        this.setState({ phase: "gone" });
      }, 2000);
    }
  }

  render() {
    if (this.state.phase !== "gone") {
      return (
        <div
          style={{
            width: "100%",
            height: "100%",
            left: "0px",
            top: "0px",
            backgroundColor: "black",
            position: "absolute",
            zIndex: 10,
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",

            animation:
              this.state.phase === "fading" ? "fadeout 1.0s forwards" : "",
            pointerEvents: this.state.phase === "visible" ? "inherit" : "none"
          }}
          onClick={() => {
            this.startFade();
          }}
        >
          <div style={{ fontSize: "15vmin" }}>
            <FontAwesomeIcon style={{ margin: "10px" }} icon={faHeadphones} />
          </div>
          <div style={{ fontSize: "5vmin" }}>HEADPHONES RECOMMENDED</div>
        </div>
      );
    }
    return <></>;
  }
}

class App extends React.Component<
  {},
  {
    aspectRatio: number;
    video: {
      url: string;
      onFinish: () => void;
      onClose: () => void;
      skippable: boolean;
    } | null;
  }
> {
  constructor(props) {
    super(props);

    const width = window.innerWidth;
    const height = window.innerHeight;

    this.state = { aspectRatio: width / height, video: null };

    window.onresize = () => {
      const width = window.innerWidth;
      const height = window.innerHeight;

      this.setState(state => ({
        aspectRatio: width / height,
        video: state.video
      }));
    };
  }

  componentDidMount() {
    const root = document.getElementById("root")!;
    root.style.background = `url(${require("./images/background.png")}) no-repeat center center`;
    root.style.backgroundSize = "cover";

    const spinner = document.getElementById("loading-spinner")!;
    spinner.style.display = "none";

    orientation.requestPortrait();
  }

  render() {
    const RouterType = MODE === "dev" ? HashRouter : MemoryRouter;
    const router = (
      <VideoPlayerContext.Provider
        value={(video, onFinish, onClose, skippable) =>
          this.setState(state => ({
            aspectRatio: state.aspectRatio,
            video: { url: video, onFinish, onClose, skippable }
          }))
        }
      >
        <ToastProvider
          placement="bottom-center"
          autoDismiss={true}
          autoDismissTimeout={5000}
          components={{ Toast: Snack }}
        >
          <RouterType>
            <AnimatePresence>
              <Switch>
                <Route path="/" exact component={Menu} />

                <Route path="/video/:id" component={VideoPage} />
                <Route path="/videos" exact component={Feed} />

                <Route path="/signup" exact component={Signup} />
                <Route path="/credits" exact component={Credits} />
                <Route path="/ending" exact component={Ending} />
              </Switch>
            </AnimatePresence>
          </RouterType>
        </ToastProvider>
      </VideoPlayerContext.Provider>
    );

    let mobileMode =
      (DETECTIONS.mobile && !DETECTIONS.iPad) ||
      IS_CORDOVA ||
      this.state.aspectRatio < MOBILE_MODE_ASPECT_RATIO;

    const videoPlayer = this.state.video ? (
      <VideoPlayer
        video={this.state.video.url}
        onClose={() => {
          this.state.video!.onClose();
          this.setState(state => ({
            aspectRatio: state.aspectRatio,
            video: null
          }));
        }}
        onFinish={() => {
          this.state.video!.onFinish();
          this.setState(state => ({
            aspectRatio: state.aspectRatio,
            video: null
          }));
        }}
        skippable={this.state.video!.skippable}
      />
    ) : null;

    let appView: React.ReactElement | null = null;
    if (mobileMode) {
      appView = (
        <>
          <div
            id="app-view"
            className="app-background"
            style={{
              overflowY: "scroll",
              scrollbarWidth: "none",
              overflowX: "hidden",
              height: "100%",
              fontSize:
                Math.ceil(
                  Math.min(window.innerWidth, window.innerHeight) / 30
                ) + "px",
              zIndex: 0
            }}
          >
            {router}
          </div>
        </>
      );
    } else {
      const height = window.innerHeight;
      const width = height * PHONE_ASPECT_RATIO;
      appView = (
        <>
          <div
            id="app-view"
            className="app-background"
            style={{
              left: window.innerWidth / 2 - width / 2 + "px",
              overflowY: "scroll",
              scrollbarWidth: "none",
              overflowX: "hidden",
              position: "absolute",
              height: height + "px",
              width: width + "px",
              fontSize: Math.ceil(width / 30) + "px",
              boxShadow: "0px 0px 10px 5px black",
              zIndex: 0
            }}
          >
            {router}
          </div>
        </>
      );
    }

    return (
      <>
        {appView}
        {videoPlayer}

        <HeadphonesWarning />

        <div
          style={{
            position: "fixed",
            right: "15px",
            bottom: "15px",
            fontSize: "2em"
          }}
        >
          <span
            style={{
              cursor: "pointer",
              display: !mobileMode ? "inherit" : "none"
            }}
            onClick={() => {
              if (window.document.fullscreenElement) {
                window.document.exitFullscreen();
              } else {
                window.document.documentElement!.requestFullscreen();
              }
            }}
          >
            <FontAwesomeIcon style={{ margin: "10px" }} icon={faExpand} />
          </span>
        </div>
      </>
    );
  }
}

ReactDOM.render(<App />, document.getElementById("root"));
