import * as React from "react";
import * as sound from "../sound";
import Logo from "./logo";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faExclamationTriangle } from "@fortawesome/free-solid-svg-icons";
import { Link } from "react-router-dom";

import { state } from "../gamestate";

export default class Ending extends React.Component<
  any,
  {
    fadeInBlack: boolean;
    fadeInLogo: boolean;
    fadeInName: boolean;
    fadeInMenu: boolean;
  }
> {
  constructor(props) {
    super(props);

    this.state = {
      fadeInBlack: false,
      fadeInLogo: false,
      fadeInName: false,
      fadeInMenu: false
    };
  }

  componentDidMount() {
    state.setCompleted();
    sound.pauseAmbience();

    setTimeout(() => {
      sound.startEndingMusic();
    }, 5000);

    setTimeout(() => {
      this.setState({ fadeInBlack: true });
    }, 9000);

    setTimeout(() => {
      this.setState({ fadeInLogo: true });
    }, 9000 + 6000);

    setTimeout(() => {
      this.setState({ fadeInName: true });
    }, 9000 + 6000 + 6000);

    setTimeout(() => {
      this.setState({ fadeInMenu: true });
    }, 9000 + 6000 + 6000 + 12000);
  }

  componentWillUnmount() {
    sound.resumeAmbience();
  }

  render() {
    let { history } = this.props;

    return (
      <>
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            height: "100%",
            alignItems: "center",
            fontSize: "200%",
            padding: "1em",
            boxSizing: "border-box"
          }}
          className="credits"
        >
          <FontAwesomeIcon
            style={{ fontSize: "300%", marginBottom: "0.4em", color: "red" }}
            icon={faExclamationTriangle}
          />
          <p>Error Code:</p>
          <p>598 (SERVER ERROR)</p>
          <p>Error Message:</p>
          <p>WE CAN FINALLY REST</p>
        </div>
        <div
          style={{
            position: "fixed",
            left: "0px",
            top: "0px",
            width: "100%",
            height: "100%",
            backgroundColor: "black",
            animation: this.state.fadeInBlack ? "fadein 5s" : "",
            opacity: this.state.fadeInBlack ? 1 : 0
          }}
        >
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              height: "100%",
              alignItems: "center",
              textAlign: "center"
            }}
            className="credits"
          >
            <div
              style={{
                animation: this.state.fadeInLogo ? "fadein 5s" : "",
                opacity: this.state.fadeInLogo ? 1 : 0
              }}
            >
              <Logo fontSize={400} />
            </div>
            <p
              style={{
                marginTop: "10px",
                animation: this.state.fadeInName ? "fadein 5s" : "",
                opacity: this.state.fadeInName ? 1 : 0,
                fontSize: "160%",
                padding: "1em"
              }}
            >
              Created by Varun R.
            </p>

            <p
              style={{
                marginTop: "10px",
                animation: this.state.fadeInMenu ? "fadein 5s" : "",
                opacity: this.state.fadeInMenu ? 1 : 0,
                pointerEvents: this.state.fadeInMenu ? "auto" : "none"
              }}
            >
              <Link
                to={`/`}
                onClick={() => {
                  sound.stopEndingMusic();
                }}
              >
                [Return to Menu]
              </Link>
            </p>
          </div>
        </div>
      </>
    );
  }
}
