import * as React from "react";

import { videos, readableNumber, Video } from "../videos";

// @ts-ignore
import nothumbnailURL from "../nothumbnail.svg";

// @ts-ignore
import avatarURL from "../avatar.svg";

import { Link } from "react-router-dom";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPaperPlane,
  faChevronLeft,
  faPlay,
  faThumbsUp,
  faThumbsDown,
  faFlag
} from "@fortawesome/free-solid-svg-icons";
import { state } from "../gamestate";

import { withToastManager } from "react-toast-notifications";

import { VideoPlayerContext } from "./videoplayer";
import * as sound from "../sound";

import { motion } from "framer-motion";

const Divider = () => {
  return (
    <div
      style={{
        width: "100%",
        marginBottom: "0.5em",
        marginTop: "0.5em",
        height: "0.1em",
        backgroundColor: "#383838"
      }}
    />
  );
};

const AVATAR_SIZE = "2.5em";

@withToastManager
export default class VideoPage extends React.Component<any> {
  comment: React.RefObject<HTMLInputElement>;

  constructor(props) {
    super(props);
    this.comment = React.createRef();
  }

  componentDidMount() {
    let { match, toastManager } = this.props;
    let video = videos.get(match.params.id);

    let isFirstOpen = !state.getCurrentUser().openedVideos.has(video.id);

    state.openVideo(video.id);
    if (video.onOpen) {
      video.onOpen({ isFirstOpen, toastManager });
    }

    document.getElementById("app-view")!.scrollTop = 0;
  }

  render() {
    let { match, toastManager, history } = this.props;
    let video = videos.get(match.params.id);

    return (
      <motion.div
        key="video-page"
        initial={{ transform: "translateX(100%)" }}
        animate={{ transform: "translateX(0%)" }}
        exit={{ transform: "translateX(100%)" }}
      >
        <div className="navbar">
          <Link
            style={{ fontSize: "180%" }}
            to="/videos"
            onClick={() => {
              sound.click();
            }}
          >
            <FontAwesomeIcon icon={faChevronLeft} />
          </Link>
        </div>

        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center"
          }}
        >
          <div style={{ width: "100%", maxWidth: "100vmin" }}>
            <div className="responsive-16by9" style={{ position: "relative" }}>
              <img
                style={{ width: "100%", position: "absolute" }}
                src={video.thumbnail || nothumbnailURL}
              />
              {video.video ? (
                <VideoPlayerContext.Consumer>
                  {play => (
                    <div
                      style={{
                        position: "absolute",
                        left: "50%",
                        top: "50%",
                        padding: "1em",
                        backgroundColor: "rgba(18, 18, 18, 0.4)",
                        transform: "translate3d(-50%, -50%, 0)",
                        borderRadius: "0.4em",
                        fontSize: "3em",
                        display: "flex",
                        alignItems: "center",
                        justifyContent: "center",
                        cursor: "pointer"
                      }}
                      onClick={() => {
                        sound.click();
                        if (video.onPlay) {
                          video.onPlay();
                        }

                        let videoURL = video.video!;
                        if (
                          !state.getCurrentUser().puzzleMode &&
                          video.initialVideo &&
                          state.getCurrentUser().getViews(video.id) === 0
                        ) {
                          videoURL = video.initialVideo;
                        }

                        let skippable =
                          video.initiallySkippable ||
                          state.getCurrentUser().getViews(video.id) > 0;

                        play(
                          videoURL,
                          () => {
                            if (video.onWatch) {
                              video.onWatch({ toastManager, history });
                            }
                            state.addView(video.id);
                          },
                          () => {
                            state.addView(video.id);
                          },
                          skippable
                        );
                      }}
                    >
                      <FontAwesomeIcon icon={faPlay} />
                    </div>
                  )}
                </VideoPlayerContext.Consumer>
              ) : (
                <div
                  style={{
                    position: "absolute",
                    left: "50%",
                    top: "50%",
                    padding: "1.5em",
                    backgroundColor: "rgba(18, 18, 18, 0.6)",
                    transform: "translate3d(-50%, -50%, 0)"
                  }}
                >
                  An error occurred. Please try again later.
                </div>
              )}
            </div>

            <div
              style={{
                paddingLeft: "15px",
                paddingRight: "15px",
                paddingTop: "10px"
              }}
            >
              <div style={{ fontSize: "160%" }}>{video.title}</div>
              <div className="subtitle">
                {readableNumber(
                  video.views + state.getCurrentUser().getViews(video.id)
                )}{" "}
                views
              </div>
              <div
                style={{
                  display: "flex",
                  justifyContent: "space-evenly",
                  paddingTop: "10px"
                }}
                className="subtitle"
              >
                <div
                  className={
                    state.getCurrentUser().likedVideos.has(video.id)
                      ? "activated"
                      : "unactivated"
                  }
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    state.likeVideo(video.id);
                    sound.click();
                    this.forceUpdate();
                    if (video.onLike) {
                      video.onLike({ toastManager });
                    }
                  }}
                >
                  <FontAwesomeIcon size="2x" icon={faThumbsUp} />
                  <div style={{ paddingTop: "5px", textAlign: "center" }}>
                    {readableNumber(
                      video.likes +
                        (state.getCurrentUser().likedVideos.has(video.id)
                          ? 1
                          : 0)
                    )}
                  </div>
                </div>
                <div
                  className={
                    state.getCurrentUser().dislikedVideos.has(video.id)
                      ? "activated"
                      : "unactivated"
                  }
                  style={{ cursor: "pointer", textAlign: "center" }}
                  onClick={() => {
                    state.dislikeVideo(video.id);
                    sound.click();
                    this.forceUpdate();
                  }}
                >
                  <FontAwesomeIcon size="2x" icon={faThumbsDown} />
                  <div style={{ paddingTop: "5px", textAlign: "center" }}>
                    {readableNumber(
                      video.dislikes +
                        (state.getCurrentUser().dislikedVideos.has(video.id)
                          ? 1
                          : 0)
                    )}
                  </div>
                </div>
                <div
                  className={
                    state.getCurrentUser().flaggedVideos.has(video.id)
                      ? "activated"
                      : "unactivated"
                  }
                  style={{ cursor: "pointer" }}
                  onClick={() => {
                    state.toggleFlag(video.id);
                    sound.click();
                    this.forceUpdate();
                  }}
                >
                  <FontAwesomeIcon size="2x" icon={faFlag} />
                  <div style={{ paddingTop: "5px" }}>Flag</div>
                </div>
              </div>
            </div>
            <Divider />
            <div
              style={{
                paddingLeft: "15px",
                paddingRight: "15px",
                paddingTop: "10px"
              }}
            >
              <div className="subtitle">
                {video.description || "(no description)"}
              </div>
            </div>
            <Divider />
            <div
              style={{
                paddingLeft: "15px",
                paddingRight: "15px",
                paddingTop: "10px"
              }}
            >
              <span style={{ fontSize: "130%" }}>Comments</span>
              <span
                className="subtitle"
                style={{ paddingLeft: "15px", fontSize: "130%" }}
              >
                {video.comments.length}
              </span>
            </div>
            {video.comments
              .concat(state.getCurrentUser().getComments(video.id))
              .map((comment, i) => (
                <div key={i}>
                  <div
                    style={{
                      paddingLeft: "15px",
                      paddingRight: "15px",
                      paddingTop: "10px",
                      display: "flex"
                    }}
                  >
                    <div style={{ paddingRight: "15px" }}>
                      <img
                        style={{ height: AVATAR_SIZE, borderRadius: "50%" }}
                        src={avatarURL}
                      />
                    </div>
                    <div>
                      <div
                        style={{ paddingBottom: "5px" }}
                        className="subtitle"
                      >
                        {comment.user}
                      </div>
                      <div>{comment.comment}</div>
                    </div>
                  </div>
                  <Divider />
                </div>
              ))}
            <div
              style={{
                paddingLeft: "15px",
                paddingRight: "20px",
                paddingTop: "10px",
                paddingBottom: "20px",
                display: "flex",
                alignItems: "center"
              }}
            >
              <div style={{ paddingRight: "15px" }}>
                <img
                  style={{ height: AVATAR_SIZE, borderRadius: "50%" }}
                  src={avatarURL}
                />
              </div>
              <input
                style={{ marginRight: "15px", flexGrow: 1 }}
                type="text"
                placeholder="Write a comment..."
                ref={this.comment}
                onKeyDown={e => {
                  if (e.key === "Enter") {
                    this.submitComment(video, toastManager);
                  }
                }}
              />
              <div
                onClick={() => {
                  this.submitComment(video, toastManager);
                }}
              >
                <FontAwesomeIcon icon={faPaperPlane} />
              </div>
            </div>
          </div>
        </div>
      </motion.div>
    );
  }

  submitComment(video: Video, toastManager) {
    let comment = this.comment.current!.value;
    if (comment.length > 0) {
      state.addComment(video.id, comment);
      sound.click();
      this.forceUpdate();
    }
    this.comment.current!.value = "";
    if (video.onComment) {
      video.onComment(comment, { toastManager });
    }
  }
}
