import * as React from "react";
import { motion } from "framer-motion";
import { videos, Video } from "../videos";
import VideoThumb from "./videothumb";
import { state } from "../gamestate";
import Logo from "./logo";
import { Link } from "react-router-dom";

import * as sound from "../sound";

// @ts-ignore
import avatarURL from "../avatar.svg";
import {
  faArrowDown,
  faChevronCircleDown,
  faChevronDown,
  faChevronUp,
  faSignOutAlt
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

class Navbar extends React.Component<{}, { navbar: boolean }> {
  constructor(props) {
    super(props);
    this.state = { navbar: false };
  }
  render() {
    let hints = getHintsForPhase(state.getCurrentUser().phase);
    return (
      <div
        style={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between"
        }}
        className="navbar"
      >
        <Logo fontSize={180} />
        <span
          style={{
            display: "flex",
            alignItems: "center"
          }}
        >
          <span style={{ paddingRight: "0.6em", fontSize: "120%" }}>
            {state.currentUser}
          </span>{" "}
          <img
            style={{ height: "1.8em", borderRadius: "50%" }}
            src={avatarURL}
          />
          <div
            onClick={() => {
              this.setState({
                navbar: !this.state.navbar
              });
              sound.click();
            }}
            style={{
              cursor: "pointer"
            }}
          >
            <FontAwesomeIcon
              style={{ margin: "10px" }}
              icon={this.state.navbar ? faChevronUp : faChevronDown}
            />
          </div>
          {this.state.navbar ? (
            <motion.div
              key="video-page"
              initial={{ opacity: 0 }}
              animate={{ opacity: 1 }}
            >
              <div
                style={{
                  position: "absolute",
                  boxShadow: "0px 0px 2px 1px black",
                  right: "5px",
                  top: "3em",
                  flexDirection: "column",
                  width: "15em",
                  display: "flex",

                  paddingTop: "0.5em",
                  paddingBottom: "0.5em"
                }}
                className="navbar-color"
              >
                <Link
                  to={`/`}
                  style={{
                    paddingRight: "1em",
                    paddingLeft: "1em",
                    paddingTop: "0.5em",
                    paddingBottom: "0.5em",
                    width: "100%"
                  }}
                  className="dropdown-item"
                  onClick={() => {
                    sound.click();
                  }}
                >
                  <FontAwesomeIcon icon={faSignOutAlt} />
                  <span style={{ paddingLeft: "0.5em" }}>Sign Out</span>
                </Link>
                <div
                  style={{
                    width: "100%",
                    marginBottom: "0.5em",
                    marginTop: "0.5em",
                    height: "0.2em",
                    backgroundColor: "#383838"
                  }}
                />
                {hints.length > 0 ? (
                  hints.map((hint, i) => {
                    return (
                      <details
                        style={{
                          paddingRight: "1em",
                          paddingLeft: "1em",
                          paddingTop: "0.5em",
                          paddingBottom: "0.5em"
                        }}
                        className="dropdown-item"
                      >
                        <summary>Reveal Hint #{i + 1}</summary>
                        <div style={{ paddingTop: "0.5em" }}>{hint}</div>
                      </details>
                    );
                  })
                ) : (
                  <div
                    style={{
                      paddingRight: "1em",
                      paddingLeft: "1em",
                      paddingTop: "0.5em",
                      paddingBottom: "0.5em"
                    }}
                    className="dropdown-item"
                  >
                    No hints available.
                  </div>
                )}
              </div>
            </motion.div>
          ) : null}
        </span>
      </div>
    );
  }
}

function getHintsForPhase(phase: number): Array<string> {
  if (phase === 3) {
    return [
      "A four digit code might open the door.",
      "Do you notice any numbers?",
      "How can you send the door code to the uploader?"
    ];
  }

  if (phase === 4) {
    return [
      "Do you see any unusual patterns?",
      "Do you know the BrickBlock secret code?"
    ];
  }

  if (phase === 5) {
    return ["That Arnie's Nighmare video was great! I want more!"];
  }

  if (phase === 6) {
    return [
      "You need another four digit code. Do you notice any other numbers?"
    ];
  }

  return [];
}

function getVideosForPhase(phase: number): Array<Video | "DIVIDER"> {
  let vids: Array<Video | "DIVIDER"> = [];

  if (phase >= 0) {
    vids.push(videos.get("movie-theory"));
    vids.push(videos.get("app-compilation"));
    vids.push(videos.get("feminist-destroyed"));
  }

  if (phase >= 1) {
    vids.push("DIVIDER");
    vids.push(videos.get("taking-a-break"));
    vids.push(videos.get("hallway-walk"));
    vids.push(videos.get("mr-creature"));
  }

  if (phase >= 2) {
    vids.push("DIVIDER");
    vids.push(videos.get("storytime"));
    vids.push(videos.get("hallway-look-behind"));
    vids.push(videos.get("wrong-right"));
  }

  if (phase >= 3) {
    vids.push("DIVIDER");
    vids.push(videos.get("hallway-door"));
    vids.push(videos.get("recipes"));
    vids.push(videos.get("bad-horror"));
    vids.push(videos.get("great-or-hate"));
    vids.push(videos.get("cat-rescue"));
  }

  if (phase >= 4) {
    vids.push("DIVIDER");
    vids.push(videos.get("block-game"));
    vids.push(videos.get("cinematic-failure"));
    vids.push(videos.get("entering-vents"));
  }

  if (phase >= 5) {
    vids.push("DIVIDER");
    vids.push(videos.get("blackscreen"));
    vids.push(videos.get("arnies-nightmare-1"));
    vids.push(videos.get("terminal-password"));
  }

  if (phase >= 6) {
    vids.push("DIVIDER");
    vids.push(videos.get("game-chums"));
    vids.push(videos.get("arnies-nightmare-2"));
  }

  if (phase >= 7) {
    vids.push("DIVIDER");
    vids.push(videos.get("brains"));
  }

  if (phase >= 8) {
    vids.push("DIVIDER");
    vids.push(videos.get("bonus"));
  }

  return vids;
}

let video_scroll = 0;
export default class Feed extends React.Component<{}, {}> {
  componentDidMount() {
    const appView = document.getElementById("app-view")!;
    appView.scrollTop = video_scroll;
  }

  componentWillUnmount() {
    const appView = document.getElementById("app-view")!;
    video_scroll = appView.scrollTop;
  }

  render() {
    return (
      <motion.div
        key="videos-page"
        initial={{ transform: "translateX(-100%)" }}
        animate={{ transform: "translateX(0%)" }}
        exit={{ transform: "translateX(-100%)" }}
      >
        <Navbar />
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            padding: "1em"
          }}
        >
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              width: "100%",
              maxWidth: "100vmin"
            }}
          >
            {getVideosForPhase(state.getCurrentUser().phase).map((video, i) =>
              video === "DIVIDER" ? (
                <div
                  key={i}
                  style={{
                    width: "100%",
                    marginBottom: "1.6em",
                    height: "0.2em",
                    backgroundColor: "#383838"
                  }}
                />
              ) : (
                <VideoThumb key={video.id} video={video} />
              )
            )}
            <div key="no-more-videos">No More Videos</div>
          </div>
        </div>
      </motion.div>
    );
  }
}
