import * as React from "react";

// @ts-ignore
import logoURL from "../images/logo.svg";

export default class Logo extends React.Component<{ fontSize: number }, {}> {
  render() {
    let size = this.props.fontSize;
    return (
      <span
        style={{ display: "flex", alignItems: "center", fontSize: size + "%" }}
      >
        <img style={{ height: "0.85em" }} src={logoURL} />
        <b style={{ paddingLeft: "0.2em" }}>FeedVid</b>
      </span>
    );
  }
}
