import { Howl } from "howler";
import { state } from "./gamestate";

const PING = new Howl({
  preload: true,
  src: [require("./audio/ping.mp3")]
});
export function notification() {
  PING.play();
}

const CLICK_SOUND = new Howl({
  preload: true,
  src: [require("./audio/plink.mp3")]
});
export function click() {
  CLICK_SOUND.play();
}

const ERROR_SOUND = new Howl({
  preload: true,
  src: [require("./audio/error.mp3")]
});
export function error() {
  ERROR_SOUND.volume(state.getCurrentUser().puzzleMode ? 0.05 : 1.0);
  ERROR_SOUND.play();
}

const ENDING_MUSIC = new Howl({
  src: [require("./audio/ending-music.mp3")]
});

export function startEndingMusic() {
  ENDING_MUSIC.play();
}
export function stopEndingMusic() {
  ENDING_MUSIC.stop();
}

const AMBIENCE = new Howl({
  src: [require("./audio/ambience.mp3")],
  volume: 0.05,
  autoplay: true,
  loop: true
});

export function resumeAmbience() {
  AMBIENCE.play();
}

export function pauseAmbience() {
  AMBIENCE.pause();
}
