import * as React from "react";

// @ts-ignore
import nothumbnailURL from "../nothumbnail.svg";

import { HashRouter as Router, Route, Link, Switch } from "react-router-dom";

import * as sound from "../sound";

import { readableNumber, Video, videos } from "../videos";

import { state } from "../gamestate";

export default class VideoThumb extends React.Component<
  { video: Video },
  { creepyTitle: boolean }
> {
  title: React.RefObject<HTMLInputElement> = React.createRef();

  constructor(props) {
    super(props);

    let video = props.video;
    let user = state.getCurrentUser();
    this.state = {
      creepyTitle:
        !user.puzzleMode && !user.seenTitle.has(video.id) && !!video.creepyTitle
    };
  }

  scrollHandler?: () => void;

  componentDidMount() {
    if (this.state.creepyTitle) {
      const appView = document.getElementById("app-view")!;

      this.scrollHandler = () => {
        let title = this.title.current;
        if (title) {
          let position = title.getBoundingClientRect();
          if (position.top < window.innerHeight && position.bottom >= 0) {
            state.sawTitle(this.props.video.id);
            setTimeout(() => {
              this.setState({ creepyTitle: false });
            }, 500);
          }
        }
      };
      appView.addEventListener("scroll", this.scrollHandler);
    }
  }

  componentWillUnmount() {
    if (this.scrollHandler) {
      const appView = document.getElementById("app-view")!;
      appView.removeEventListener("scroll", this.scrollHandler);
    }
  }

  render() {
    let video = this.props.video;
    let borderStyle: any = {};
    if (video.personalized) {
      borderStyle = {
        borderStyle: "solid",
        borderWidth: "4px",
        borderColor: "#3EA5FF",
        boxSizing: "border-box"
      };
    }
    return (
      <Link
        to={`/video/${video.id}`}
        onClick={() => {
          sound.click();
        }}
        style={{ width: "100%" }}
      >
        <div style={{ paddingBottom: "1.6em" }}>
          {video.personalized && (
            <span
              style={{
                backgroundColor: "#3EA5FF",
                color: "white",
                padding: "2px 4px 2px 4px",
                fontSize: "100%"
              }}
            >
              PERSONALIZED
            </span>
          )}

          <div
            style={{
              ...borderStyle,
              marginBottom: "5px",
              width: "100%",
              paddingTop: "56.25%",
              backgroundImage: `url(${video.thumbnail || nothumbnailURL})`,
              backgroundSize: "cover"
            }}
          />
          <div ref={this.title} style={{ fontSize: "160%" }}>
            {this.state.creepyTitle ? video.creepyTitle : video.title}
          </div>
          <div className="subtitle" style={{ fontSize: "130%" }}>
            {video.author} &bull;{" "}
            {readableNumber(
              video.views + state.getCurrentUser().getViews(video.id)
            )}{" "}
            views
          </div>
        </div>
      </Link>
    );
  }
}
