export const NAMES_TO_AUDIO = new Map();
for (let [file, names] of AUDIO_TO_NAMES) {
  for (let name of names) {
    NAMES_TO_AUDIO.set(name, file);
  }
}

function getSoundFromFullName(fullName) {
  if (!fullName) return;

  let tokens = fullName.match(/\S+/g);
  tokens = tokens.map(token => token.toLowerCase());
  tokens = tokens.map(token =>
    token.normalize("NFD").replace(/[\u0300-\u036f]/g, "")
  );
  for (let token of tokens) {
    if (NAMES_TO_AUDIO.has(token)) return NAMES_TO_AUDIO.get(token);
  }
}

const VOLUME = 0.4;

export function playRealName() {
  let fullName = Promise.resolve(state.getCurrentUser().username);
  if (isElectron()) {
    fullName = require("fullname")();
  }

  fullName.then(name => {
    let soundFile = getSoundFromFullName(name);
    if (soundFile) {
      new Howl({
        src: [soundFile],
        volume: VOLUME
      }).play();
    } else {
      new Howl({
        src: [require("./audio/names/help.mp3")],
        volume: VOLUME
      }).play();
    }
  });
}

