import * as log from "loglevel";

const ORIENTATION_SUPPORTED = new Boolean(
  window.screen && window.screen.orientation && window.screen.orientation.lock
);

export async function requestPortrait() {
  console.log("Requesting portrait orientation...");
  try {
    if (ORIENTATION_SUPPORTED) {
      await window.screen.orientation.lock("portrait");
    }
  } catch (e) {
    log.warn(e);
  }
}

export async function requestLandscape() {
  console.log("Requesting landscape orientation...");
  try {
    if (ORIENTATION_SUPPORTED) {
      await window.screen.orientation.lock("landscape");
    }
  } catch (e) {
    log.warn(e);
  }
}
