import * as React from "react";
import Logo from "./logo";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { Link } from "react-router-dom";
import { state } from "../gamestate";
import * as sound from "../sound";

import { motion } from "framer-motion";

import { faInfoCircle, faStar } from "@fortawesome/free-solid-svg-icons";

export default class Menu extends React.Component<any, {}> {
  render() {
    let { history } = this.props;
    return (
      <motion.div
        key="menu-page"
        initial={{ transform: "translateX(-100%)" }}
        animate={{ transform: "translateX(0%)" }}
        exit={{ transform: "translateX(-100%)" }}
        style={{ height: "100%" }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            height: "100%",
            alignItems: "center"
          }}
        >
          <Logo fontSize={400} />
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              justifyContent: "center",
              alignItems: "align"
            }}
          >
            {state.currentUser ? (
              <>
                <button
                  style={{
                    fontSize: "130%",
                    padding: "0.8em",
                    marginTop: "1.3em"
                  }}
                  onClick={() => {
                    sound.click();
                    history.push("/videos");
                  }}
                >
                  LOG IN AS <b>{state.currentUser}</b>
                </button>
              </>
            ) : (
              <></>
            )}

            <button
              onClick={() => {
                sound.click();
                history.push("/signup");
              }}
              type="button"
              style={{ fontSize: "130%", padding: "0.8em", marginTop: "1.3em" }}
            >
              SIGN UP
            </button>
          </div>

          <div
            style={{
              position: "absolute",
              left: "15px",
              bottom: "15px",
              fontSize: "3em"
            }}
          >
            <span
              style={{ cursor: "pointer" }}
              onClick={() => {
                sound.click();
                history.push("/credits");
              }}
            >
              <FontAwesomeIcon style={{ margin: "10px" }} icon={faInfoCircle} />
            </span>
          </div>

          {state.completed ? (
            <div
              style={{
                position: "absolute",
                right: "15px",
                top: "15px",
                fontSize: "3em"
              }}
            >
              <FontAwesomeIcon style={{ margin: "10px" }} icon={faStar} />
            </div>
          ) : null}
        </div>
      </motion.div>
    );
  }
}
