import { Comment } from "./videos";

const SAVE_FILE_KEY = "FEEDVID_SAVE_KEY";

export class User {
  username: string;
  phase: number;
  comments: Map<string, Array<string>>;
  openedVideos: Set<string>;

  flaggedVideos: Set<string>;
  likedVideos: Set<string>;
  dislikedVideos: Set<string>;

  views: Map<string, number>;

  // Whether or not a video's title was ever visible as it scrolled by in the feed.
  // This is used for the creepy subliminal titles.
  seenTitle: Set<string>;

  puzzleMode: boolean;

  static create(username: string, puzzleMode: boolean): User {
    return new User(
      username,
      0,
      new Map(),
      new Set(),
      new Set(),
      new Set(),
      new Set(),
      new Map(),
      new Set(),
      puzzleMode
    );
  }

  static fromJSON(json: any): User {
    return new User(
      json.username,
      json.phase,
      new Map(json.comments),
      new Set(json.openedVideos),
      new Set(json.flaggedVideos),
      new Set(json.likedVideos),
      new Set(json.dislikedVideos),
      new Map(json.views),
      new Set(json.seenTitle),
      json.puzzleMode
    );
  }

  constructor(
    username: string,
    phase: number,
    comments: Map<string, Array<string>>,
    openedVideos: Set<string>,
    flaggedVideos: Set<string>,
    likedVideos: Set<string>,
    dislikedVideos: Set<string>,
    views: Map<string, number>,
    seenTitle: Set<string>,
    puzzleMode: boolean
  ) {
    this.username = username;
    this.phase = phase;
    this.comments = comments;

    this.openedVideos = openedVideos;

    this.flaggedVideos = flaggedVideos;
    this.likedVideos = likedVideos;
    this.dislikedVideos = dislikedVideos;

    this.views = views;
    this.seenTitle = seenTitle;

    this.puzzleMode = puzzleMode;
  }

  toJSON() {
    return {
      username: this.username,
      comments: [...this.comments],
      phase: this.phase,
      openedVideos: [...this.openedVideos],

      flaggedVideos: [...this.flaggedVideos],
      likedVideos: [...this.likedVideos],
      dislikedVideos: [...this.dislikedVideos],

      views: [...this.views],

      seenTitle: [...this.seenTitle],

      puzzleMode: this.puzzleMode
    };
  }

  addComment(video: string, comment: string) {
    let vid_comments: Array<string> = this.comments.get(video) || [];
    vid_comments.push(comment);
    this.comments.set(video, vid_comments);
  }

  getComments(video: string): Array<Comment> {
    return (this.comments.get(video) || []).map(comment => ({
      user: this.username,
      comment
    }));
  }

  openVideo(video: string) {
    this.openedVideos.add(video);
  }

  setPhase(phase: number) {
    this.phase = phase;
  }

  toggleFlag(video: string) {
    if (this.flaggedVideos.has(video)) {
      this.flaggedVideos.delete(video);
    } else {
      this.flaggedVideos.add(video);
    }
  }

  likeVideo(video: string) {
    this.dislikedVideos.delete(video);

    if (this.likedVideos.has(video)) {
      this.likedVideos.delete(video);
    } else {
      this.likedVideos.add(video);
    }
  }

  dislikeVideo(video: string) {
    this.likedVideos.delete(video);
    if (this.dislikedVideos.has(video)) {
      this.dislikedVideos.delete(video);
    } else {
      this.dislikedVideos.add(video);
    }
  }

  getViews(video: string): number {
    return this.views.get(video) || 0;
  }

  addView(video: string) {
    this.views.set(video, this.getViews(video) + 1);
  }

  sawTitle(video: string) {
    this.seenTitle.add(video);
  }
}

export class GameState {
  currentUser: string | null;
  users: Map<string, User>;
  completed: boolean;

  static create(): GameState {
    return new GameState(null, new Map(), false);
  }

  static fromJSON(json: any): GameState {
    if (json.version === 1) {
      let users = new Map(
        json.users.map(([username, user]) => [username, User.fromJSON(user)])
      );
      return new GameState(json.currentUser, users, json.completed);
    } else {
      throw new Error("Unknown save data version number.");
    }
  }

  static load(): GameState {
    let save: any = null;
    try {
      save = window.localStorage.getItem(SAVE_FILE_KEY);
    } catch (e) {
      console.warn("Failed to load save data.");
    }

    if (save) {
      try {
        console.log("Loading save data...");
        return GameState.fromJSON(JSON.parse(save));
      } catch (e) {
        alert(`Error parsing save data: ${e.message}`);
        return GameState.create();
      }
    } else {
      console.log("No save data found... Creating save data...");
      return GameState.create();
    }
  }

  constructor(currentUser, users, completed) {
    this.currentUser = currentUser;
    this.users = users;
    this.completed = completed;
  }

  newUser(username: string, puzzleMode: boolean) {
    this.users.set(username, User.create(username, puzzleMode));
    this.save();
  }

  loginAs(username: string) {
    this.currentUser = username;
    this.save();
  }

  logout() {
    this.currentUser = null;
    this.save();
  }

  setCompleted() {
    this.completed = true;
    this.save();
  }

  getCurrentUser(): User {
    if (this.currentUser) {
      return this.users.get(this.currentUser)!;
    } else {
      throw new Error("Not logged in.");
    }
  }

  addComment(video: string, comment: string) {
    this.getCurrentUser().addComment(video, comment);
    this.save();
  }

  openVideo(video: string) {
    this.getCurrentUser().openVideo(video);
    this.save();
  }

  setPhase(phase: number) {
    this.getCurrentUser().setPhase(phase);
    this.save();
  }

  toggleFlag(video: string) {
    this.getCurrentUser().toggleFlag(video);
    this.save();
  }

  likeVideo(video: string) {
    this.getCurrentUser().likeVideo(video);
    this.save();
  }

  dislikeVideo(video: string) {
    this.getCurrentUser().dislikeVideo(video);
    this.save();
  }

  addView(video: string) {
    this.getCurrentUser().addView(video);
    this.save();
  }

  sawTitle(video: string) {
    this.getCurrentUser().sawTitle(video);
    this.save();
  }

  toJSON() {
    return {
      version: 1,
      currentUser: this.currentUser,
      users: [...this.users].map(([username, user]) => [
        username,
        user.toJSON()
      ]),
      completed: this.completed
    };
  }

  save() {
    try {
      let json = this.toJSON();
      window.localStorage.setItem(SAVE_FILE_KEY, JSON.stringify(json));
    } catch (e) {
      console.warn("Failed to save data.");
    }
  }
}

export const state = GameState.load();

// Install on global window object for debugging purposes.
window["GAME_STATE"] = state;
