import * as React from "react";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTimesCircle } from "@fortawesome/free-solid-svg-icons";

import { motion } from "framer-motion";

import * as sound from "../sound";
import { DETECTIONS } from "../detections";
import * as orientation from "../orientation";

const spinner = document.getElementById("loading-spinner")!;

export const VideoPlayerContext: React.Context<
  (
    video: string,
    onFinish: () => void,
    onClose: () => void,
    skippable: boolean
  ) => void
> = React.createContext((video, onFinish, onClose, skippable) => {});

function pad(n: number): string {
  // @ts-ignore
  return n.toString().padStart(2, "0");
}

function formatTime(position: number): string {
  let seconds = Math.floor(position);

  let minutes = Math.floor(seconds / 60);
  seconds -= minutes * 60;

  let hours = Math.floor(minutes / 60);
  minutes -= hours * 60;

  return `${pad(hours)}:${pad(minutes)}:${pad(seconds)}`;
}

export class VideoPlayer extends React.Component<
  {
    video: string;
    onClose: () => void;
    onFinish: () => void;
    skippable: boolean;
  },
  { position: number; loaded: boolean }
> {
  video: React.RefObject<HTMLVideoElement>;

  constructor(props) {
    super(props);

    this.video = React.createRef();
    this.state = { position: 0, loaded: false };
  }

  componentDidMount() {
    spinner.style.display = "inherit";

    sound.pauseAmbience();

    this.video.current!.onplaying = () => {
      spinner.style.display = "none";
      this.setState({ loaded: true });
    };
    this.video.current!.play();
    this.video.current!.ontimeupdate = () => {
      if (this.video.current) {
        this.setState({ position: this.video.current.currentTime });
      }
    };
    this.video.current!.onended = () => {
      this.props.onFinish();
    };

    orientation.requestLandscape();
  }

  componentWillUnmount() {
    sound.resumeAmbience();

    orientation.requestPortrait();
  }

  render() {
    const coverStyle = {
      position: "fixed",
      minHeight: "100%",
      minWidth: "100%",
      zIndex: 3,
      left: "50%",
      top: "50%",
      transform: "translate3d(-50%, -50%, 0)"
    };

    let player = (
      <div
        style={{
          position: "fixed",
          left: "0px",
          top: "0px",
          width: "100%",
          height: "100%",
          backgroundColor: "black",
          zIndex: 2
        }}
      >
        {this.props.skippable ? (
          <>
            <div
              style={{
                position: "fixed",
                right: "0px",
                top: "0px",
                fontSize: "6vw",
                padding: "1vw",
                cursor: "pointer",
                opacity: 0.4,
                zIndex: 5
              }}
              onClick={() => {
                spinner.style.display = "none";
                sound.click();
                this.props.onClose();
              }}
            >
              <span>
                <FontAwesomeIcon
                  style={{ margin: "10px" }}
                  icon={faTimesCircle}
                />
              </span>
            </div>
            <div
              style={{
                position: "fixed",
                left: "0px",
                top: "0px",
                fontSize: "4vw",
                padding: "2vw",
                opacity: 0.4,
                zIndex: 4,
                display: this.state.loaded ? "inherit" : "none"
              }}
            >
              {this.video.current
                ? formatTime(this.state.position) +
                  " / " +
                  formatTime(this.video.current!.duration)
                : formatTime(0) + " / " + formatTime(0)}
            </div>
          </>
        ) : null}

        <video
          ref={this.video}
          style={{
            width: "100%",
            height: "100%",
            zIndex: 3
          }}
          src={this.props.video}
          playsInline={true}
          controls={false}
          disableRemotePlayback={true}
          disablePictureInPicture={true}
        />
      </div>
    );

    return (
      <motion.div
        key="video-player"
        initial={{ transform: "scale(0, 0)" }}
        animate={{ transform: "scale(1, 1)" }}
        exit={{ transform: "scale(0, 0)" }}
        transition={{ type: "tween", duration: 0.1 }}
        style={{
          width: "100%",
          height: "100%",

          position: "fixed",
          left: "0px",
          top: "0px",

          zIndex: 2
        }}
      >
        {player}
      </motion.div>
    );
  }
}
