const DIRECTIONS = new Set([
  "up",
  "right",
  "down",
  "left",
  "forward",
  "north",
  "east",
  "south",
  "west"
]);
const deepEqual = require("deep-equal");

videos.register({
  id: "entering-vents",
  title: "VID-00004.mp4",
  author: RECORDING_USER,
  views: 0,
  likes: 0,
  dislikes: 0,
  comments: [],
  video: require("./video/entering-vents.mp4"),
  description: "need directions for vents",
  onComment: (comment, { toastManager }) => {
    let tokens = comment.match(/[a-z0-9]+/gi);
    tokens = tokens.map(token => token.toLowerCase());
    tokens = tokens.filter(token => DIRECTIONS.has(token));

    let ACCEPTED_ANSWERS = [
      // Absolute directions.
      ["up", "right", "down", "right", "right", "up", "up", "left", "up", "up"],
      // Absolute compass directions.
      [
        "north",
        "east",
        "south",
        "east",
        "east",
        "north",
        "north",
        "west",
        "north",
        "north"
      ],
      // Relative directions
      [
        "right",
        "right",
        "left",
        "forward",
        "left",
        "forward",
        "left",
        "right",
        "forward",
        "forward"
      ],
      [
        "right",
        "right",
        "left",
        "up",
        "left",
        "up",
        "left",
        "right",
        "up",
        "up"
      ],
      // Relative directions, no last up
      [
        "right",
        "right",
        "left",
        "forward",
        "left",
        "forward",
        "left",
        "right",
        "forward"
      ],
      [
        "right",
        "right",
        "left",
        "forward",
        "left",
        "forward",
        "left",
        "right",
        "forward"
      ],
      // Relative direcions only at choice points.
      ["right", "right", "forward"],
      ["right", "right", "up"]
    ];

    let correct = ACCEPTED_ANSWERS.some(answer => deepEqual(tokens, answer));
    console.log(correct ? "Answer accepted..." : "Answer not accepted...");

    let user = state.getCurrentUser();
    if (user.phase == 4 && correct) {
      state.setPhase(5);
      setTimeout(() => notifyNewVideos(toastManager), 2000);
    }
  },
  personalized: true,
  initiallySkippable: false
});

